
import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'main',
        component: () => import("../views/Main.vue"),
        redirect: '/index',
        children: [
            {
                path: '/introduction',
                component: () => import("../views/Introduction.vue")
            },
            {
                path: '/wordList',
                component: () => import("../views/WordList.vue")
            },
            {
                path: '/index',
                component: () => import("../views/myIndex.vue"),
            },
            {
                path: '/calender',
                component: () => import("../views/MyCalender.vue"),
            },
            {
                path: '/reciteWords',
                component: () => import("../views/MyWords.vue"),
            },
            {
                path: '/addWords',
                component: () => import("../views/AddWords.vue"),
            },
            {
                path: '/addGroup',
                component: () => import("../views/AddGroup.vue"),
            },
            {
                path: '/allWordList',
                component: () => import("../views/allWordList.vue")
            },
            {
                path: '/login',
                component: () => import("../views/MyLogin.vue")
            },
            {
                path: '/register',
                component: () => import("../views/MyRegister.vue")
            },
        ]
    },

]

const router = new VueRouter({
    // mode: 'history',
    routes
})

export default router